* {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, .5) transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(155, 155, 155, .5);
  border-radius: 20px;
  border: transparent;
}

.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  background-color: white;
  box-shadow: 2px 2px 50px rgb(168, 168, 168);
  overflow: scroll;
  transform: translateX(-100%);
  transition: all .5s ease;
  opacity: 0;
  h2 {
    padding: 2rem;
  }
}

.library-song {
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: background .5s ease;
  img {
    width: 30%;
  }
  &:hover {
    background: rgb(195, 240, 226);
  }
}

.song-des {
  padding-left: 1rem;

  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: .7rem;
  }
}

.selected {
  background: rgb(244, 187, 244);
}

.active {
  transform: translateX(0);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
  }
}